:root {
    --tg-bg-color: #1a1a2e; /* Тёмно-синий фон */
    --tg-text-color: #f5f5f5; /* Светло-серый текст */
    --tg-secondary-text-color: #f5f5f5; /* Второстепенный текст */
    --tg-card-bg: #232946; /* Фон карточек */
    --tg-button-bg: #0f4c81; /* Тёмно-синий для кнопок */
    --tg-button-text: #ffffff; /* Белый текст для кнопок */
    --tg-border-color: #4caf50; /* Цвет границ (зелёный) */
}

/* Общий контейнер */
.fish-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
    /*background: linear-gradient(to bottom, #eef7ff, #d6e6f5);*/
    font-family: 'Poppins', sans-serif;
    /*color: #333;*/
    background: var(--tg-bg-color);
    color: var(--tg-text-color);
}

/* Header с кнопкой назад */
.fish-header {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 600px;
    margin-bottom: 20px;
}

.fish-back-button {
    display: flex;
    align-items: center;
    gap: 5px; /* Space between icon and text */
    background: none;
    border: none;
    font-size: 1rem;
    /*color: #4caf50;*/
    color: var(--tg-text-color);
    cursor: pointer;
    transition: color 0.2s ease, transform 0.2s ease;
}

.fish-back-button:hover {
    color: #0056b3; /* Darker shade on hover */
    transform: translateX(-5px); /* Subtle movement */
}

.fish-title {
    font-size: 20px;
    font-weight: bold;
    /*color: #333;*/
    color: var(--tg-text-color);
    margin: 0;
}

/* Контейнер с изображением */
.fish-header-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 25px;
}
/* Категории */
.categories {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 30px;
}

.category {
    padding: 10px 20px;
    border: 2px solid #4caf50;
    /*border: 2px solid var(--tg-border-color);*/
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 16px;
    font-weight: 500;
    color: #4caf50;
    /*color: var(--tg-border-color);*/
    background: var(--tg-bg-color);
    text-align: center;
}


.category.selected {
    border-color: #28a745;
    /*background: #e8f8ea;*/
    font-weight: bold;
    /*color: #28a745;*/
    background: #4caf50;
    color: #f5f5f5;
}

/* Количество */
.quantity-selector {
    padding: 10px;
    border-radius: 12px;
    width: 100%;
    max-width: 400px;
    background: var(--tg-card-bg);
    color: var(--tg-text-color);
}

.quantity-controls {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.quantity-button {
    background: #b0bfd5;
    color: white;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.quantity-value {
    font-size: 20px;
    font-weight: 600;
    color: #333;
}

.quantity-price {
    margin-top: 10px;
    font-size: 18px;
    font-weight: 600;
    color: #4caf50;
    text-align: center;
}

/* Разделитель */
.section-divider {
    width: 100%;
    max-width: 400px;
    height: 1px;
    background-color: #ddd;
    margin: 20px 0;
}
